.popup {
	height: 100vh;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background-color: rgba($color-black, 0.8);
	z-index: 9999;
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s;

	@supports (-webkit-backdrop-filter: blur(10px)) or (backdrop-filter: blur(10px)) {
		-webkit-backdrop-filter: blur(10px);
		backdrop-filter: blur(10px);
		background-color: rgba($color-black, 0.3);
	}

	&__content {
		@include absCenter;

		width: 75%;
		background-color: $color-white;
		box-shadow: 0 2rem 4rem rgba($color-black, 0.2);
		border-radius: 3px;
		display: table;
		overflow: hidden;
		opacity: 0;
		transform: translate(-50%, -50%) scale(0);
		transition: all 0.5s 0.2s;
	}

	&__left {
		width: 33.33333333333%;
		display: table-cell;

		@include respond(tab-land) {
			display: table-row;
			width: 100%;
		}

		@include respond(phone) {
			display: none;
		}
	}

	&__right {
		width: 66.66666666667%;
		display: table-cell;
		vertical-align: middle;
		padding: 3rem 5rem;

		@include respond(tab-land) {
			width: 100%;
		}

		@include respond(phone) {
			padding: 1.5rem 3.5rem;
		}
	}

	&__img {
		display: block;
		width: 100%;

		@include respond(tab-land) {
			width: 50%;
			display: inline-block;

			&:first-child {
				width: 50.8%;
			}

			&:last-child {
				width: 49.2%;
			}
		}
	}

	&__text {
		margin-bottom: 4rem;
		font-size: 1.4rem;
		column-count: 2;
		column-gap: 4rem;
		column-rule: 1px solid $color-grey-light-2;
		-webkit-hyphens: auto;
		-moz-hyphens: auto;
		-ms-hyphens: auto;
		hyphens: auto;

		@include respond(phone) {
			margin-bottom: 2rem;
			column-count: 1;
		}
	}

	//OPEN STATES
	&:target {
		opacity: 1;
		visibility: visible;
	}

	&:target &__content {
		opacity: 1;
		transform: translate(-50%, -50%) scale(1);
	}

	&__close {
		&:link,
		&:visited {
			color: $color-grey-dark;
			position: absolute;
			top: 2.5rem;
			right: 2.5rem;
			font-size: 3rem;
			text-decoration: none;
			display: inline-block;
			transition: all 0.2s;
			cursor: pointer;
			line-height: 1;

			@include respond(tab-port) {
				top: 2rem;
				right: 2rem;
			}

			@include respond(phone) {
				top: 1.5rem;
				right: 1.5rem;
			}
		}

		&:hover,
		&:active {
			color: $color-primary;
		}
	}
}
